.footer {
  padding: 30px 0;
  background-color: $color-mariner;

  &__wrapper {
    @include vp-768 {
      display: flex;
      flex-wrap: wrap;
      gap: 1.5rem;
    }

    @include vp-1280 {
      display: grid;
      row-gap: 0.5rem;
      column-gap: 1rem;
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &__block {
    @include vp-768 {
      width: calc(50% - 1.5rem);
    }

    @include vp-1024 {
      width: calc(33% - 1.5rem);
    }

    @include vp-1280 {
      width: 100%;
    }
  }

  &__nav {
    margin-bottom: 1.3rem;

    @include vp-1280 {
      grid-column: 1 / span 8;
      grid-row: 1 / span 2;
    }
  }

  &__nav-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1rem;
  }

  &__nav-link {
    font-size: 14px;
    line-height: 21px;
    color: $color-default-white;
    border-bottom: 1px solid $color-default-white;
    transition: border-color $trans-default, color $trans-default;

    &:hover,
    &:focus {
      color: rgba($color-default-white, 0.7);
      border-color: rgba($color-default-white, 0.7);
    }

    &:active {
      color: rgba($color-default-white, 0.5);
      border-color: rgba($color-default-white, 0.5);
    }
  }

  &__text {
    font-size: 13px;
    line-height: 19px;
    color: $color-default-white;
    margin: 0;
    margin-bottom: 1.3rem;

    @include vp-1280 {
      grid-column: 1 / span 8;
    }

    &--law {
      @include vp-1280 {
        grid-row: 3;
      }
    }

    &--license {
      @include vp-1280 {
        grid-row: 4;
      }
    }
  }

  &__right-box {
    margin-bottom: 0.7rem;

    @include vp-1280 {
      grid-column: 9 / span 4;
      grid-row: 1 / span 3;
      margin-bottom: 0;
    }
  }

  &__right-box-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 0.7rem;
  }

  &__right-box-link {
    display: block;
    width: 75px;
    height: 75px;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  &__control {
    padding-left: 85px;

    @include vp-768 {
      padding: 0;
      margin-left: auto;
    }

    @include vp-1280 {
      margin-left: 0;
      grid-column: 9 / span 4;
      grid-row: 3;
      align-self: flex-end;
      justify-self: end;
    }
  }

  &__control-link {
    display: block;
    height: 67px;
    width: 245px;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }

    @include vp-768 {
      margin-left: auto;
    }

    @include vp-1280 {
      display: flex;
      align-items: flex-end;
      justify-self: flex-end;
      margin-top: 1rem;
    }
  }
}
