.files {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: flex;
    gap: 0.8rem;
    padding: 1rem;
  }

  &__icon {
    width: 50px;
    height: 50px;
  }

  &__link {
    display: block;
    margin-bottom: 4px;
    width: 100%;
    font-size: 13px;
    line-height: 19px;
    color: $color-cerulean;
    border-bottom: 1px solid transparent;
    transition: border-color $trans-default, color $trans-default;

    &:hover,
    &:focus {
      border-color: $color-cerulean;
    }

    &:active {
      color: rgba($color-cerulean, 70%);
      border-color: rgba($color-cerulean, 70%);
    }
  }

  &__size {
    display: block;
    font-size: 10px;
    line-height: 15px;
    color: $color-link-water;
  }
}
