.social-box {
  &--mb {
    margin-bottom: 1.2rem;

    @include vp-1024 {
      margin-bottom: 0;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;

    @include vp-1024 {
      justify-content: space-between;
    }
  }

  &__link {
    transition: opacity $trans-default;

    & img,
    & svg {
      width: 27px;
      height: 27px;
    }

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
