.main-nav {
  &__wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;

    @include vp-1024 {
      padding: 0;
      flex-direction: row;
      align-items: center;
      justify-content: space-evenly;
      gap: 1rem;
      width: 100%;
    }
  }

  &__list {
    display: block;
    margin: 0;
    margin-bottom: auto;
    padding: 0;
    list-style: none;

    @include vp-1024 {
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      margin-bottom: 0;
      width: 50%;
    }
  }

  &__item {
    margin-bottom: 0.7rem;
    margin-right: 1.2rem;

    @include vp-1024 {
      margin: 0;
      position: relative;
    }

    &.is-active {
      & .main-nav__link {
        margin-bottom: 0.8rem;

        @include vp-1024 {
          margin-bottom: 0;
        }
      }

      & .main-nav__subbox {
        // height: calc(auto);
        display: block;

        @include vp-1024 {
          display: block;
          height: auto;
          max-height: none;
        }
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 21px;
    line-height: 31px;
    color: $color-cerulean;
    transition: color $trans-default;

    &::after {
      content: "";
      margin-left: 5px;
      border: solid $color-light-sea-green;
      border-width: 0 3px 3px 0;
      display: inline-block;
      vertical-align: middle;
      padding: 3px;
      transform: rotate(-45deg);
      transition: transform $trans-default, color $trans-default;
    }

    &:hover,
    &:focus {
      color: $color-flamenco;

      &::after {
        border-color: $color-flamenco;
        transform: rotate(45deg);
      }
    }

    &:active {
      &::after {
        border-color: rgb($color-flamenco, 0.7);
      }

      color: rgba($color-flamenco, 0.7);
    }

    &.is-active {
      opacity: 0.6;
    }

    @include vp-1024 {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      align-items: baseline;
      min-width: 90px;
    }
  }

  &__toggle {
    display: none;
  }

  &__phone {
    display: block;
    text-align: center;
    font-size: 19px;
    line-height: 19px;
    color: $color-nero;
    margin-bottom: 0.6rem;
    transition: color $trans-default;

    &:hover,
    &:focus {
      color: rgba($color-nero, 0.7);
    }

    &:active {
      color: rgba($color-nero, 0.5);
    }

    @include vp-1024 {
      margin-bottom: 1rem;
    }
  }

  &__subbox {
    // transition: height $trans-default;
    // height: 0;
    display: none;

    @include vp-1024 {
      display: none;
      overflow: visible;
      width: 100vw;
      position: fixed;
      margin: 0;
      left: 0;
      right: 0;
      top: 134px;
      background: $color-default-white;
      min-height: 240px;
      max-height: none;
    }
  }

  &__subbox-wrap {
    @include vp-1024 {
      display: flex;
      width: 100%;
      padding-bottom: 2em;
      padding-top: 1em;
    }
  }

  &__subtitle-box {
    @include vp-1024 {
      min-width: 350px;
      padding-top: 0.75rem;
    }
  }

  &__subtitle {
    display: none;

    @include vp-1024 {
      display: inline-block;
      vertical-align: middle;
      align-self: flex-start;
      margin: 0;
      font-family: $ff-placeholder-2;
      font-size: 26px;
      line-height: 39px;
      color: $color-cerulean;
      border-bottom: 1px solid $color-cerulean;
    }
  }

  &__sublist {
    list-style: none;
    margin: 0;
    padding: 0;

    @include vp-1024 {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      gap: 1rem;
      padding: 0;
      padding-top: 1.25rem;
      margin-left: 1.9rem;
      width: 100%;
    }
  }

  &__subitem {
    margin-bottom: 0.4rem;

    @include vp-1024 {
      width: 30%;
    }
  }

  &__sublink {
    color: $color-cerulean;
    border-bottom: 1px solid transparent;
    transition: border-color $trans-default, color $trans-default;
    font-family: $ff-placeholder-2;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;

    &::after {
      content: "";
      margin-left: 5px;
      border: solid $color-light-sea-green;
      border-width: 0 3px 3px 0;
      display: inline-block;
      vertical-align: middle;
      padding: 3px;
      transform: rotate(-45deg);
      transition: transform $trans-default, color $trans-default;
    }

    &:hover,
    &:focus {
      border-color: $color-cerulean;
    }

    &:active {
      color: rgba($color-cerulean, 0.9);
      border-color: rgba($color-cerulean, 0.9);
    }
  }
}
