.bg {
  position: absolute;
  left: 0;
  right: 0;
  height: 100%;
  background-image: linear-gradient(0deg, $color-lochmara, $color-light-sea-green);
  background-color: $color-default-black;
  z-index: 10;
  overflow: hidden;
  opacity: 0.8;
}

.is-hidden {
  display: none;
}
