.main-actions {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    margin-bottom: 0.5rem;
  }

  &__link {
    display: block;
    width: 100%;
    font-size: 18px;
    line-height: 27px;
    color: $color-cerulean;
    text-align: center;
    transition: color $trans-default;

    &:hover,
    &:focus {
      color: rgba($color-cerulean, 0.7);
    }

    &:active {
      color: rgba($color-cerulean, 0.5);
    }

    @include vp-1024 {
      font-size: 16px;
      font-weight: 500px;
      line-height: 24px;
    }

    &--fill {
      font-weight: 500;
      text-transform: uppercase;
      color: $color-default-white;
      background-color: $color-light-sea-green;
      border: 1px solid $color-light-sea-green;
      padding: 8px 16px;
      border-radius: 2px;
      transition: background-color $trans-default;

      &:hover,
      &:focus {
        color: rgba($color-default-white, 0.7);
        background-color: rgba($color-light-sea-green, 0.9);
      }

      &:active {
        color: rgba($color-default-white, 0.5);
        background-color: rgba($color-light-sea-green, 0.7);
      }

      @include vp-1024 {
        font-size: 13px;
        font-weight: 500px;
        line-height: 19.5px;
      }
    }
  }
}
