.header {
  border-bottom: 2px solid $color-cerulean;
  padding: 20px 0;
  padding-bottom: 18px;

  @include vp-1024 {
    background-color: $color-default-white;
    padding: 27px 0;
    position: relative;
    z-index: 20;
  }

  .container {
    display: flex;
  }

  &__wrapper {
    position: relative;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include vp-1024 {
      justify-content: space-between;
      align-items: center;
      flex-wrap: nowrap;
      gap: 2rem;
    }
  }

  &__logo {
    display: block;
    width: 190px;
    height: 54px;
    color: $color-default-white;

    @include vp-1024 {
      width: 210px;
      height: 50px;
      flex-shrink: 0;
    }
  }

  &__toggle {
    position: relative;
    background-color: transparent;
    padding: 0;
    border: 0;
    order: -1;
    display: flex;
    margin-right: 20px;
    cursor: pointer;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.7;
    }

    & svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(50%);
      width: 30px;
      height: 25px;
      transition: opacity $trans-default;
    }

    &--open {
      opacity: 1;
    }

    &--close {
      opacity: 0;
    }

    &.is-active {
      & .header__toggle--close {
        opacity: 1;
      }

      & .header__toggle--open {
        opacity: 0;
      }
    }

    @include vp-1024 {
      display: none;
    }
  }

  &__nav {
    position: fixed;
    overflow: hidden;
    overflow-y: scroll;
    width: 100%;
    max-height: 0;
    top: 100px;
    left: 0;
    right: 0;
    z-index: 2;
    background-color: $color-default-white;
    transition: max-height $trans-default;

    &.is-active {
      height: calc(100% - 100px);
      max-height: 100vh;
    }

    @include vp-1024 {
      position: static;
      overflow: visible;
      max-height: none;
      display: flex;
      justify-content: space-between;
      gap: 1rem;
    }
  }

  &__contact-box {
    @include vp-1024 {
      flex-shrink: 0;
    }
  }
}
