.breadcrumbs {
  width: 100%;

  &__list {
    list-style: none;
    margin: 0;
    padding: 0.5rem 1rem;
    display: flex;
    flex-wrap: wrap;
    padding-left: 1em;
    background-color: $color-default-white;
    width: 100%;
    border-radius: 8px;
    align-items: center;
    gap: 1rem;
  }

  &__item {
    &--arrow {
      &::after {
        content: "";
        display: inline-flex;
        border-color: $color-light-sea-green;
        border-style: solid;
        border-width: 0.1em 0.1em 0 0;
        height: 6px;
        width: 6px;
        transform: rotate(45deg);
        margin-left: 1rem;
      }
    }
  }

  &__link {
    color: $color-cerulean;
    font-size: 13px;
    line-height: 19px;
    font-weight: 500;
    border-bottom: 1px solid transparent;
    transition: border-color $trans-default, color $trans-default;

    &:hover,
    &:focus {
      border-bottom-color: $color-cerulean;
    }

    &:active {
      color: rgba($color-cerulean, 70%);
      border-color: rgba($color-cerulean, 70%);
    }
  }

  &__text {
    color: $color-nero;
    font-size: 13px;
    font-weight: 500;
    line-height: 19px;
  }
}
