.page-content {
  &--mb {
    margin-top: 1em;
    margin-bottom: 2em;
  }

  &__wrapper {
    display: flex;
    gap: 0.95rem;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__main {
    width: 100%;

    @include vp-1024 {
      width: 83%;
    }

    @include vp-1280 {
      width: 73.5%;
    }
  }

  &__title {
    margin-left: -37px;
    margin-right: -37px;
    margin-bottom: 20px;
    padding: 20px 37px;
    border-bottom: 1px solid $color-ghost-white;
  }

  &__addition {
    width: 100%;

    @include vp-1024 {
      width: 15%;
    }

    @include vp-1280 {
      width: 25%;
    }
  }
}
