@keyframes pulse {
  0% {
    margin-left: 5px;
  }

  100% {
    margin-left: 12px;
  }
}

.addition-links {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    margin-bottom: 1.3rem;
  }

  &__link {
    &:hover,
    &:focus {
      & .addition-links__wrapper {
        &::after {
          @include vp-1280 {
            animation: pulse 1s ease-in-out;
            animation-fill-mode: forwards;
          }
        }
      }
    }
  }

  &__wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &::after {
      content: "";
      margin-left: auto;
      border-color: $color-light-sea-green;
      border-style: solid;
      border-width: 0.25em 0.25em 0 0;
      display: inline-block;
      vertical-align: middle;
      height: 25px;
      width: 25px;
      transform: rotate(45deg);
      flex-shrink: 0;

      @include vp-1280 {
        margin-left: 5px;
      }
    }

    @include vp-1024 {
      position: relative;
    }

    @include vp-1280 {
      position: static;
    }
  }

  &__icon {
    width: 52px;
    height: 52px;
    margin-right: 0.95rem;
  }

  &__text {
    @include vp-1024 {
      position: absolute;

      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;

      white-space: nowrap;

      border: 0;

      clip: rect(0 0 0 0);
      clip-path: inset(100%);
    }

    @include vp-1280 {
      position: static;
      height: auto;
      margin: 0;
      overflow: visible;
      white-space: normal;
      clip: auto;
      clip-path: none;

      max-width: 190px;
      width: 100%;
      font-size: 14px;
      line-height: 21px;
      color: $color-dim-gray;
    }
  }
}
